<template>
  <div class="services">
    <div class="titles">
      <div id="3" class="title"> {{services[3]["title"]}}</div>
      <div id="2" class="title">{{services[2]["title"]}}</div>
      <div id="1" class="title">{{services[1]["title"]}}</div>
      <div id="0" class="title">{{services[0]["title"]}}</div>
    </div>
    <div class="description"></div>
    <services-illustration/>
  </div>
</template>

<script>
import $ from 'jquery'
import ServicesIllustration from '@/components/ServicesIllustration.vue'

  export default {
    components: {
      ServicesIllustration
    },
    data() {
      return {
        activeService: "",
        services: [
          {
            title: "Design",
            color: "#FF6464",
            description: "kube10 designs anything and everything for your web-related projects, including logo's, UI, illustrations, animations and icons."
          },
          {
            title: "Coding",
            color: "#88D483",
            description: "We write custom code that will bring your vision to life. Based on your needs, we use a CMS like WordPress, or build your website completely from scratch."
          },
          {
            title: "Content",
            color: "#FEBC1E",
            description: "Using our network of creative minds, we can help you come up with great content for your customers, that will keep them engaged."
          },
          {
            title: "Support",
            color: "#527BCE",
            description: "Kube10 offers continuous support after publishing your website. This way we guarantee your project remains future proof."
          }
        ]
      }
    },
    methods: {
      animateIllustration(id, delay) {
        $('.illustration-group').removeClass('active');
        setTimeout(function () {
          if (id === "0") {
            $('#design-illustration').addClass('active');
          } else if (id === "1") {
            $('#coding-illustration').removeClass('base');
            $('#coding-illustration').addClass('active');
          } else if (id === "2") {
            $("#content-illustration").removeClass('base');
            $('#content-illustration').addClass('active');
          } else if (id === "3") {
            $("#support-illustration").removeClass('base');
            $('#support-illustration').addClass('active');
          }
        }, delay);
      },
      animateTitle(id) {
        $("#" + id + ".title").css('color', this.services[id]['color']);
        $("#" + id + ".title").css('font-size', '10em');
      },
      removeAnimateTitle() {
        $(".title").css('color', "black");
        $(".title").css('font-size', '3.8em');
      },
      animateDescription(id) {
        var vm = this;
        $('.description').animate({
          opacity: 0,
          left: "55%"
        }, 300, function() {
          $('.description').html(vm.services[id]['description']);
          $('.description').animate({
            opacity: 1,
            left: "60%"
          }, 300);
        });
      }
    },
    mounted() {
      var vm = this;
      vm.removeAnimateTitle();
      vm.animateTitle(0);
      vm.animateDescription(0);
      vm.animateIllustration("0", 0);
      $('.logo .small').css('background-color', '#F5F5F5');
      $(".title").on('click', function(){
        var i = $(this).attr('id');
        vm.removeAnimateTitle();
        vm.animateTitle(i);
        vm.animateDescription(i);
        vm.animateIllustration(i, 500);
      });
    }
  }
</script>

<style>
  .titles {
    position: absolute;
    bottom: 10px;
    right: 20px;
  }

   .title:nth-child(1):hover {
     color: #527BCE !important;
   }

   .title:nth-child(2):hover {
     color: #FEBC1E !important;
   }

   .title:nth-child(3):hover {
     color: #88D483 !important;
   }

   .title:nth-child(4):hover {
     color: #FF6464 !important;
   }

  .description {
    font-size: 1.2em;
    width: 400px;
    line-height: 1.617;
    position: absolute;
    top: 40%;
    transform: translateY(-50%);
    left: 60%;
    font-family: 'Lato', sans-serif;
  }

  .title {
    text-transform: uppercase;
    font-weight: 900;
    text-align: right;
    font-size: 3.8em;
    transition: font-size .5s ease-in, color .3s ease;
    cursor: pointer;
  }

</style>
